import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useState } from 'react';
import {
    Container,
    Stack,
    Card,
    Box, Grid, Alert,
    Typography, IconButton
} from '@mui/material';
import { FileCopy as FileCopyIcon } from '@mui/icons-material';
import swal from 'sweetalert';
import { useForm } from 'react-hook-form';
import { LoadingButton } from '@mui/lab';
import clipboardCopy from 'clipboard-copy';
import { PATH_DASHBOARD } from '../../routes/paths';
import useSettings from '../../hooks/useSettings';
import axios from '../../utils/axios';
import useAuth from '../../hooks/useAuth';
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { FormProvider, RHFTextField, } from '../../components/hook-form';



export default function DynamicAccount() {
    const NewUserSchema = Yup.object().shape({
        amount: Yup.string().required('Amount is required'),
    });
    const AccessToken = window.localStorage.getItem('accessToken');
    const { themeStretch } = useSettings();
    const { user } = useAuth();
    const [setaccount, accountyes] = useState(false);
    const [accountMpa, setaccountMap] = useState(null);
    const methods = useForm({
        resolver: yupResolver(NewUserSchema),
    });
    const {
        handleSubmit,
        formState: { errors, isSubmitting },
    } = methods;

    const handleCopyAccountNumber = () => {
        clipboardCopy(accountMpa?.account_number);
        alert('Account number copied to clipboard!');
    };
    const handleReload = () => {
        // Assuming PATH_DASHBOARD.root is the path to your dashboard
        window.location.href = PATH_DASHBOARD.root;
    };

    const onSubmit = async (data) => {
        try {
            const isConfirmed = await swal({
                title: 'Confirmation',
                text: `Please note that, the account number that will  be provided shortly. Once received, it will be a one-time account number. Kindly transfer the exact amount of ₦${data.amount} into the provided account`,
                icon: 'warning',
                buttons: ['Cancel', 'Confirm'],
                dangerMode: true,
            });
            if (isConfirmed) {
                const resp = await axios.post('/api/dynamic-account-number-here/system/secure', {
                    data, token: AccessToken,
                });
                accountyes(true);
                setaccountMap(resp.data.data)
            }
        } catch (e) {
            accountyes(false);
            if (e?.data?.message !== undefined) {
                swal('Oops', `${e?.data?.message}`, 'error');
            } else if (e?.message !== undefined) {
                swal('Oops', `${e?.message}`, 'error');
            } else {
                swal('Oops', 'Something went wrong', 'error');
            }
        }
    }
    return (
        <Page title="Dynamic Account Number">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs Account Number
                    heading={'Dynamic Account Number'}
                    links={[
                        { name: 'Dashboard', href: PATH_DASHBOARD.root },
                        { name: 'Fund Wallet', href: PATH_DASHBOARD.fund.account },
                    ]}
                />
                {!setaccount ? <>
                    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                        <Grid container spacing={3}>
                            <Grid item xs={12} md={8}>
                                {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
                                <Card sx={{ p: 3 }}>
                                    <Box
                                        sx={{
                                            display: 'grid',
                                            columnGap: 1,
                                            rowGap: 3,
                                            gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
                                        }}
                                    >
                                        <RHFTextField name="amount" label="Amount" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
                                    </Box>
                                    <Stack alignItems="flex-end" sx={{ mt: 3 }}>
                                        <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                                            Generate
                                        </LoadingButton>
                                    </Stack>
                                </Card>
                            </Grid>
                        </Grid>
                    </FormProvider>
                </> : <>
                    <Card sx={{ p: 3 }}>
                        <Typography variant="h6" color="error" mb={2}>
                            Important Notice
                        </Typography>
                        <Typography variant="body1" color="error">
                            Please note that the account number provided is a one-time account number.
                            Kindly transfer the exact amount of ₦{accountMpa?.amount} into the provided account.
                        </Typography>

                        <Box mt={3}>
                            <Typography variant="body2">
                                <strong>Account Number:</strong> {accountMpa?.account_number}
                                <IconButton size="small" onClick={handleCopyAccountNumber} ml={1}>
                                    <FileCopyIcon />
                                </IconButton>
                            </Typography>
                            <Typography variant="body2">
                                <strong>Account Name:</strong> {accountMpa?.account_name}
                            </Typography>
                            <Typography variant="body2">
                                <strong>Bank Name:</strong> {accountMpa?.bank_name}
                            </Typography>
                            <Typography variant="body2">
                                <strong>Charges:</strong> ₦{accountMpa?.charges}
                            </Typography>
                            <Typography variant="body2">
                                <strong>Amount to Send:</strong> ₦{accountMpa?.amount}
                            </Typography>
                            <Typography variant="body2" sx={{ animation: 'blink 1s infinite' }}>
                                <strong>Expires at:</strong> {accountMpa?.expire_at}
                            </Typography>
                        </Box>
                        <LoadingButton fullWidth size="large" type="submit" variant="contained" onClick={handleReload}>
                            Payment Sent
                        </LoadingButton>
                    </Card>

                </>}
            </Container>
        </Page>
    );

}
